.type{
    display: flex;
    align-items: center;
    background-color: #272337;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 40px 20px;
    margin: 30px 0;
    width: 450px;
    height: 170px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.type img{
    margin-right: 20px;
}

.type h2{
    margin-bottom: 10px;
}


@media (min-width:1201px) {
    .type{
        width: 500px;
    }
}

@media (max-width:992px) {
    .type{
        width: 100%;
    }
}

a{
    color: white;
}